/** hero section **/
.hero-section {
  background-image: url("/public/hero_image.jpg");
  cursor: pointer;
  height: 350px;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.hero-msg {
  background-color: white;
  color: black;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1vw;
  width: 97%;
  margin-bottom: 3vw;
  cursor: default;
}

.hero-msg a {
  color: #007185;
  text-decoration: none;
}

.hero-msg a:hover {
  text-decoration: underline;
  color: rgb(216, 173, 93);
}

/** shopping section **/
.shop-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: #e2e7e6;
}

.box .box-content a {
  color: #000;
  font-size: 1.09vw;
  font-weight: bold;
}

.box {
  margin-top: 0.8vw;
  /* border: 2px solid black; */
  height: 400px;
  width: 23%;
  background-color: white;
  padding: 20px 0px 15px;
  cursor: pointer;
}

.box4 {
  margin-top: 0.8vw;
  height: 150px;
  width: 23%;
  background-color: white;
}

.box4-content {
  margin-left: 1vw;
  margin-right: 1vw;
}

.box4 h2 {
  display: flex;
  justify-content: center;
  margin-top: 2vw;
  font-size: 1.29vw;
  font-weight: bold;
}

.but {
  background-color: #ffd814;
  color: black;
  width: 305px;
  height: 2rem;
  border: 0px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  margin-top: 1vw;
  margin-left: 0.6vw;
}

.box4-content a {
  text-decoration: none;
  color: black;
}

.bg {
  width: 100%;
  height: 100px;
  margin-top: 5vw;
  margin-left: 1.8vw;
}

.box img {
  height: 300px;
  width: 320px;
  margin-top: 1.2vw;
  margin-bottom: 1.2vw;
  background-size: cover;
}

.box-content {
  margin-left: 1vw;
  margin-right: 1vw;
}

.box-content a {
  color: #007185;
  text-decoration: none;
  font-size: 0.85vw;
}

.box-content a p a:hover {
  color: #d47952;
}

/** footer **/
footer {
  margin-top: 0.8vw;
}

.foot-panel1 {
  background-color: #37475a;
  color: white;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.85vw;
  transition-duration: 0.4s;
}

.foot-panel1:hover {
  background-color: #485769;
}

.foot-panel2 {
  background-color: #222f3d;
  color: white;
  height: 300px;
  display: flex;
  justify-content: space-evenly;
}

.foot-panel2 ul {
  margin-top: 2vw;
}

.foot-panel2 ul a {
  color: #dddddd;
  display: block;
  text-decoration: none;
  margin-top: 0.7vw;
  font-size: 0.9vw;
}

.foot-panel2 ul a:hover {
  text-decoration: underline;
}

.foot-panel3 {
  border-top: 0.5px solid white;
  background-color: #222f3d;
  color: white;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo1 {
  background-image: url("amazon_logo.png");
  height: 50px;
  width: 100px;
  background-size: cover;
}

.foot-panel4 {
  background-color: #0f1111;
  color: white;
  height: 80px;
  font-size: 0.8vw;
  text-align: center;
}

.pages {
  padding-top: 25px;
}

.pages a {
  margin-right: 1vw;
}

.pages a:hover {
  text-decoration: underline;
}

.copyright {
  padding-top: 5px;
}

.foot-panel4 a {
  text-decoration: none;
  color: white;
}

@media (max-width: 600px) {
  .hero-msg {
    font-size: 2.5vw;
    display: block;
    padding: 1vw;
    padding-top: 2vw;
    text-align: center;
  }
  .hero-msg a {
    font-size: 2.5vw;
  }
  .navbar {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  .nav-search {
    margin-top: 8vw;
    position: absolute;
    width: 96%;
    margin-left: 1.5vw;
  }
  .nav-search input {
    font-size: 3vw;
  }
  .search-icon {
    font-size: 3vw;
    color: black;
  }
  .hero-section {
    max-width: 100%;
    height: 30vh;
  }
  .navbar {
    height: 100px;
  }
  .nav-logo {
    margin-top: -14vw;
    margin-left: -13vw;
  }
  .top-icon {
    font-size: 4.5vw;
    margin-top: -12.5vw;
    margin-left: -4vw;
    position: absolute;
    left: 7vw;
    color: white;
  }
  .top-icon a {
    color: white;
  }
  .nav-address {
    display: none;
  }
  .nav-lang {
    display: none;
  }
  .nav-return {
    display: none;
  }
  .nav-signin {
    margin-top: -12vw;
    margin-left: 11vw;
  }
  .nav-signin p span {
    font-size: 2vw;
  }
  .nav-signin p {
    font-size: 2vw;
  }
  .nav-cart {
    position: absolute;
    font-size: 5vw;
    margin-top: -12vw;
    right: 0;
    margin-right: 1vw;
  }
  .nav-cart i {
    font-size: 5vw;
  }
  .panel-all {
    margin-left: -7.5vw;
  }
  .panel-all a {
    color: white;
    text-decoration: none;
  }
  .panel-ops {
    display: flex;
    flex-direction: row;
    gap: 4vw;
    /* align-items: center;
          justify-content: space-evenly; */
  }
  .panel-deals {
    display: none;
  }
  .box-content a {
    font-size: 3vw;
  }
  .panel-all i {
    margin-left: 1.5vw;
  }
  .box {
    width: 48.5%;
    height: 380px;
  }
  .box3 {
    margin-left: -1vw;
  }
  .box4 {
    width: 46%;
    /* margin-left: 1vw; */
  }
  .box4 h2 {
    font-size: 3vw;
    text-align: center;
  }
  .but {
    font-size: 2.6vw;
    width: 70%;
    padding: 1.1vw 2.5vw;
    margin-left: 7vw;
    margin-top: 7vw;
  }
  .box4 img {
    width: 90%;
    height: 200px;
    /* margin-left: 2vw; */
    margin-top: 15vw;
  }
  .foot-panel1 {
    font-size: 3vw;
  }
  .foot-panel2 {
    gap: 3.5vw;
    flex-wrap: wrap;
  }
  .pay {
    margin-right: 19vw;
    padding-left: 2vw;
  }
  .ment {
    columns: 9vw;
    gap: 1vw;
  }
  .let {
    margin-left: -30vw;
    padding-right: 8vw;
  }
  .foot-panel2 ul a {
    font-size: 2vw;
  }
  .foot-panel4 {
    font-size: 2vw;
  }
}
@media (hover: hover) {
  .border:hover {
    border: 2px solid white;
  }
  .nav-search:hover {
    border: 2px solid orange;
  }
  .box-content a:hover {
    color: #d47952;
  }
  .foot-panel2 ul a:hover {
    text-decoration: underline;
  }
}
@media (hover: none) {
  .border:hover {
    border: inherit;
  }
  .nav-search:hover {
    border: inherit;
  }
  .box-content a:hover {
    color: inherit;
  }
  .foot-panel2 ul a:hover {
    text-decoration: inherit;
  }
}
